<template>
	<div class="scoreReport reportSec">
      <div class="rtitle">个人题型得分情况</div>
      <div class="xcontainer">
      题型分数比重分布
      <canvas id="ScorePieChart" width="400" ref="ScorePieChart" height="295"></canvas>
      </div>
      
      <div class="xcontainer">
         题型得分布
         <canvas id="SocreRadarChart" width="400" ref="SocreRadarChart" height="295"></canvas>
      </div>
      
      <div class="xcontainer">
        <div class="tableTitle">知识点详情:</div>
       <xtable :xtable="dataall.question_type_info"/>
      </div>
       
       <summarize :summarize="dataall.question_type_info.conclusion.join(' ')"/>
  </div>
</template>

<script>

import xtable from './xtable'
import summarize from './summarize'


export default {
  name: 'Score',
  props:['dataall'],
  mounted(){
    var ctx1 = this.$refs.ScorePieChart;
    var ctx2 = this.$refs.SocreRadarChart;

    var KnowledgeChart = new Chart(ctx1, {
        type: 'pie',
        data: {
            labels: [
                "Red",
                "Blue",
                "Yellow"
            ],
            datasets: [
                {
                    data: [300, 50, 100],
                    backgroundColor: [
                        "#FF6384",
                        "#36A2EB",
                        "#FFCE56"
                    ],
                    hoverBackgroundColor: [
                        "#FF6384",
                        "#36A2EB",
                        "#FFCE56"
                    ]
                }]
        }
    });
    var myRadarChart = new Chart(ctx2, {
        type: 'radar',
        data: {
          labels: ["Eating", "Drinking", "Sleeping", "Designing", "Coding", "Cycling", "Running"],
          datasets: [
              {
                  label: "My First dataset",
                  backgroundColor: "rgba(179,181,198,0.2)",
                  borderColor: "rgba(179,181,198,1)",
                  pointBackgroundColor: "rgba(179,181,198,1)",
                  pointBorderColor: "#fff",
                  pointHoverBackgroundColor: "#fff",
                  pointHoverBorderColor: "rgba(179,181,198,1)",
                  data: [65, 59, 90, 81, 56, 55, 40]
              },
              {
                  label: "My Second dataset",
                  backgroundColor: "rgba(255,99,132,0.2)",
                  borderColor: "rgba(255,99,132,1)",
                  pointBackgroundColor: "rgba(255,99,132,1)",
                  pointBorderColor: "#fff",
                  pointHoverBackgroundColor: "#fff",
                  pointHoverBorderColor: "rgba(255,99,132,1)",
                  data: [28, 48, 40, 19, 96, 27, 100]
              }
          ]
    }
    });  
  },
  components: {
    summarize,
    xtable
  }
}
</script>

<style>
.tableTitle{
  text-align: left;
  padding: 0 20px;
  color: #000;
  font-weight: bold;
}
.rtitle{
    background: #c9e5f5;
    color: #000;
    font-weight: bold;
    height: 35px;
    line-height: 35px;
    margin: 10px 0;
  }
  .xcontainer{
    margin: 10px 0;
    color: #000;
  }
</style>


